<template>
  <div class="pos">
   	<el-row>
   		<el-col :span="7" class="pos-order">
   			<el-tabs>
   				<el-tab-pane label="点餐">
   					<el-table :data="tableData" border style="width:100%">
   						<el-table-column prop="goodsName" label="商品名称"></el-table-column>
   						<el-table-column prop="count" label="数量" width="70"></el-table-column>
   						<el-table-column prop="price" label="金额" width="70"></el-table-column>

   						<el-table-column label="操作" width="100" fixed="right">
   							<template scope="scope">
   								<el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
   								<el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
   							</template>
   						</el-table-column>
   					</el-table>
   					<div class="totle">
   						<small>数量:</small>{{totleCount}}　　<small>金额:</small>{{totleMoney}}元
   					</div>
   					<div class="btn">
   						<el-button type="danger" @click="delAllGoods">清空</el-button>
   						<el-button type="warning">挂单</el-button>
   						<el-button type="success" @click="checkOut">结账</el-button>
   					</div>
   				</el-tab-pane>
   				<el-tab-pane label="挂单">
   					挂单
   				</el-tab-pane>
   				<el-tab-pane label="外卖">
   					外卖
   				</el-tab-pane>
   			</el-tabs>
   		</el-col>
   		<el-col :span="17">
   			<div>
   				<div class="title">热门商品</div>
   				<div class="list">
   					<ul>
   						<li v-for="goods in oftenGoods" @click="addOrderList(goods)">
   							<div>{{goods.goodsName}}</div>
   							<div class="price">￥: {{goods.price}} 元</div>
   						</li>
   					</ul>
   				</div>
   			</div>

   			<div>
   				<el-tabs>
   					<el-tab-pane label="汉堡">
   						<div>
   							<ul class="cook-list">
   								<li v-for="goods in type0Goods" @click="addOrderList(goods)">
   									<div><img :src="goods.goodsImg" alt=""></div>
   									<div>{{goods.goodsName}}</div>
   									<div class="price">￥: {{goods.price}} 元</div>
   								</li>
   							</ul>
   						</div>
   					</el-tab-pane>
   					<el-tab-pane label="小食">
   						<div>
   							<ul class="cook-list">
   								<li v-for="goods in type1Goods" @click="addOrderList(goods)">
   									<div><img :src="goods.goodsImg" alt=""></div>
   									<div>{{goods.goodsName}}</div>
   									<div class="price">￥: {{goods.price}} 元</div>
   								</li>
   							</ul>
   						</div>
   					</el-tab-pane>
   					<el-tab-pane label="饮品">
   						<div>
   							<ul class="cook-list">
   								<li v-for="goods in type2Goods" @click="addOrderList(goods)">
   									<div><img :src="goods.goodsImg" alt=""></div>
   									<div>{{goods.goodsName}}</div>
   									<div class="price">￥: {{goods.price}} 元</div>
   								</li>
   							</ul>
   						</div>
   					</el-tab-pane>
   					<el-tab-pane label="套餐">
   						<div>
   							<ul class="cook-list">
   								<li v-for="goods in type3Goods" @click="addOrderList(goods)">
   									<div><img :src="goods.goodsImg" alt=""></div>
   									<div>{{goods.goodsName}}</div>
   									<div class="price">￥: {{goods.price}} 元</div>
   								</li>
   							</ul>
   						</div>
   					</el-tab-pane>
   				</el-tabs>
   			</div>
   		</el-col>
   	</el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos',
  data () {
  	return {
  		tableData: [],
  		oftenGoods: [],
        type0Goods: [],
        type1Goods: [],
        type2Goods: [],
        type3Goods: [],
        totleCount: 0,
        totleMoney: 0
  	}
  },
  created: function () {
  	axios.get('http://jspang.com/DemoApi/oftenGoods.php')
  	.then(reponse=>{
  		console.log(reponse);
  		this.oftenGoods = reponse.data;
  	})
  	.catch(error=>{
  		console.log(error);
  		alert("网络错误，不能访问")
  	})

  	axios.get('http://jspang.com/DemoApi/typeGoods.php')
  	.then(reponse=>{
  		console.log(reponse);
  		this.type0Goods = reponse.data[0];
  		this.type1Goods = reponse.data[1];
  		this.type2Goods = reponse.data[2];
  		this.type3Goods = reponse.data[3];
  	})
  	.catch(error=>{
  		console.log(error);
  		alert("网络错误，不能访问")
  	})
  },
  mounted: function () {
  	var orderHeight = document.documentElement.clientHeight;
  	document.querySelector(".pos-order").style.height = orderHeight + "px";
  },
  methods: {
  	addOrderList (goods) {
  		let isHave = false;
  		for (let i = 0; i < this.tableData.length; i++) {
  			if (this.tableData[i].goodsId == goods.goodsId) {
  				isHave = true;
  			}
  		};
  		if (isHave) {
  			let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
  			arr[0].count++;
  		} else {
  			let newGoods = {goodsId: goods.goodsId, goodsName: goods.goodsName, price: goods.price,count: 1};
  			this.tableData.push(newGoods);
  		}
  		this.getAllMoney();
  	},
  	delSingleGoods (goods) {
  		this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId);
  		this.getAllMoney();
  	},
  	getAllMoney () {
  		this.totleCount = 0;
  		this.totleMoney = 0;
  		if (this.tableData) {
  			this.tableData.forEach((element)=>{
  				this.totleCount += element.count;
  				this.totleMoney += element.price * element.count;
  			})
  		}
  	},
  	delAllGoods () {
  		this.tableData = [];
  		this.totleCount = 0;
  		this.totleMoney = 0;
  	},
  	checkOut () {
  		if (this.totleCount != 0) {	
  			this.tableData = [];
  			this.totleCount = 0;
  			this.totleMoney = 0;
  			this.$message({
  				message: "结账成功，欢迎下次光临！",
  				type: "success"
  			})
  		} else {
  			this.$message.error("你还没有购买任何东西！")
  		}
  	}
  }
}
</script>


<style scoped>
    .pos-order {
    	background-color: #F9FAFC;
    	border-right: 1px solid #C0CCDA;
    }
    .title {
    	height: 20px;
    	padding: 10px;
    	background-color: #F9FAFC;
    	text-align: left;
    	font-weight: bold;
    }
    .list ul {
    	padding: 0;
    	margin: 0;
    }
    .list li {
    	list-style: none;
    	display: inline-block;
    	width: 25%;
    	font: 16px/20px "微软雅黑";
    	padding: 10px;
    	margin: 5px;
    	border-radius: 8px;
    	border: 1px solid purple;
    	background-color: #eee;
    	cursor: pointer;
    	-webkit-user-select: none;
    }
    .price {
    	color: orange;
    }
    .cook-list {
    	clear: both;
    }
    .cook-list li {
    	width: 17%;
		list-style: none;
		float: left;
    	border-radius: 8px;
    	margin: 5px;
    	cursor: pointer;
    	-webkit-user-select: none;
    }
    img {
    	width: 100%;
    	border: none;
    	border-radius: 8px;
    }
    .totle {
    	padding: 10px;
    }
</style>



